import React from 'react';
import { Link } from 'react-router-dom';
import './Navbar.css';

function Navbar() {
  return (
    <nav>
      <ul>
        <li><Link to="/"></Link></li>
        <li><Link to="/about"></Link></li>
        <li><Link to="/courses"></Link></li>
        <li><Link to="/contact"></Link></li>
      </ul>
    </nav>
  );
}

export default Navbar;
